.trendingNews-container {
    display: flex;
    height: 500px;
    width: 100%;
    position: relative;
    align-items: center;
    justify-content: center;
}

.trendingNews-img-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

.trendingNews-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center center;
    filter: brightness(0.6);
}

.trendingNews-content {
    position: absolute;
    text-align: center;
}

.trendingNews-text {
    max-width: 1280px;
    width: 100%;
}

.trendingNews-content h2,
.trendingNews-content p,
.trendingNews-content a {
    color: #fff;
    text-shadow: rgba(0, 0, 0, 0.25) 1px 1px 1px;
}

.trendingNews-content p {
    font-size: var(--h3-font-size);
    line-height: 1.75rem;
}

.trendingNews-content h2 {
    max-width: 60%;
    margin: 2.5rem auto;
    line-height: 1.42857;
}

.trendingNews-button {
    font-size: 1rem;
    padding: 0.625rem 1.5rem;
    line-height: 1.42857;
    height: auto;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 2.25rem;
    text-align: center;
}

.newsCard-container {
    padding: 5rem 1rem;
    background: rgb(247, 247, 247);
}

.news-card {
    display: grid;
    grid-template-columns: auto auto auto auto;
    gap: .8rem;
}

@media screen and (max-width: 992px) {
    .trendingNews-container {
        height: 400px;
    }

    .news-card {
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }
}

@media screen and (max-width: 768px) {
    .news-card {
        max-width: 576px;
        gap: .5rem;
    }
}

@media screen and (max-width: 576px) {
    .trendingNews-container {
        height: 350px;
    }

    .trendingNews-content h2 {
        font-size: 1.35rem;
        margin: 1.5rem auto;
    }

    .trendingNews-content p {
        font-size: var(--normal-font-size);
    }

    .trendingNews-text {
        max-width: 400px;
    }
}