.loanText-container {
    display: flex;
    flex-flow: column;
    justify-content: center;
    padding-right: 0;
    padding-left: 0;
    height: 500px;
    flex: 0 0 50%;
    max-width: 50%;
}

.loanText-container .loanText-content {
    padding: 0 3.125rem;
}

.loanText-container .loanText-content h2 {
    font-size: var(--mid-font-size);
    line-height: 2.5rem;
    margin: 0;
}

.loanText-container .loanText-content p {
    font-size: 1.25rem;
    line-height: 1.75rem;
    padding-top: 0.5rem;
    margin-bottom: 1.5rem;
}

.loanText-container .loanText-content .loanText-button {
    display: inline-flex;
    font-size: 1rem;
    line-height: 1.42857;
    height: auto;
    padding: 0.625rem 1.5rem;
    align-items: center;
    background-color: var(--primary-color);
    color: #fff;
    border: 1px solid var(--primary-color);
    border-radius: 2.25rem;
    justify-content: center;
}

.loanText-container .loanText-content .loanText-button:hover {
    background-color: #fff;
    color: var(--primary-color);
    transition: .3s ease-in-out;
}

.loanText-container .advertising-button {
    position: absolute;
    bottom: 2.25rem;
    padding-left: 3.125rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    text-decoration: underline;
    height: 36;   
}

.loanText-container .loanText-content .search-wrapper {
    max-width: 500px;
}

.loanText-container .loanText-content .realtor-link {
    display: block;
    color: rgb(30, 97, 217);
    background-color: transparent;
    border-color: transparent;
    padding: 0;
    font-size: var(--normal-font-size);
    line-height: 2.25rem;
    padding-bottom: 0.6rem;
}

.loanText-container .loanText-content .realtor-link:hover {
    text-decoration: underline;
}

@media screen and (max-width: 425px) {
    .loanText-container .loanText-content .realtor-link {
        line-height: 1.5rem;
    }
}