* { 
    box-sizing: border-box; 
}

.container {
    max-width: 1280px;
    width: 100%;
    margin: auto;
    padding: 0 1rem;
}

.card-container {
    display: flex;
    flex-wrap: wrap;
    margin-right: -1rem;
    margin-left: -1rem;
}

a {
    text-decoration: none;
    color: #000;
}

button {
    border: none;
}

.icon, button, a {
    cursor: pointer;
}

ul {
    list-style: none;
}

.card-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    font-size: var(--small-font-size);
    border-radius: 2.25rem;
    border: 1px solid var(--primary-color);
    height: 2.25rem;
    text-align: center;
    padding: 0 1rem;
    color: #fff;
    background-color: var(--primary-color);
    transition: .3s;
}

.card-button:hover {
    background-color: #fff;
    color: var(--primary-color);
}

.advertising-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    font-size: var(--small-font-size);
    height: 2.25rem;
    padding: 0;
    text-decoration: underline;
    transition: .3s;
}

.advertising-button:hover {
    color: #6b6b6b;
}

.secondary-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    font-size: var(--small-font-size);
    border-radius: 2.25rem;
    height: 2.25rem;
    text-align: center;
    padding: 0 1rem;
    transition: .3s;
    background-color: #fff;
    border: 1px solid var(--primary-color);
    color: var(--primary-color);
}

.secondary-button:hover {
    background-color: #e2e2e2;
    border-color: #f6f6f6;
    color: #000;
}

@media screen and (max-width: 1280px) {
    .container {
        max-width: 992px;
    }
}

@media screen and (max-width: 992px) {
    .container {
        max-width: 768px;
    }
}