.discover-container {
    position: relative;
    margin-bottom: 3.75rem;
}

.tab-container {
    margin-left: 0.5rem;
}

.discover-container .discover-title {
    font-size: var(--h2-font-size);
    line-height: 2rem;
    margin-top: 0;
    margin-bottom: 1.5rem;
    padding-left: 0.5rem;
}

.card-container {
    /* margin-top: 1.5rem;
    display: flex;
    justify-content: space-between; */
    margin-top: 1.5rem;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
}

.tab-button {
    font-size: 0.875rem;
    line-height: 1;
    height: 2.25rem;
    border: 1px solid rgb(117, 117, 117);
    border-radius: 2.25rem;
    padding: 0 1rem;
    color: rgb(43, 43, 43);
    background-color: #fff;
    margin-right: .6rem;
}

.tab-button:hover {
    background-color: rgb(241, 241, 241);
    border-color: rgb(241, 241, 241);
}

.active-tab {
    background-color: rgb(43, 43, 43);
    color: #fff;
}

.active-tab:hover {
    background-color: rgb(117, 117, 117);
}

@media screen and (max-width: 992px) {
    .card-container {
        display: block;
    }
}