.hero-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    color: #fff;
    height: 500px;
    width: 100%;
    margin-bottom: 3.75rem;
}

.image-container {
    position: absolute;
    display: flex;
    height: 500px;
    width: 100%;
}

.hero-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    filter: brightness(0.5);
}

.hero-text {
    position: absolute;
    text-align: center;
    padding-right: 2rem;
    padding-left: 2rem;
}

.hero-text h1 {
    font-size: var(--big-font-size);
    margin-bottom: 1rem;
    margin-top: 0;
}

.hero-text p {
    font-size: var(--h3-font-size);
    font-weight: var(--font-normal);
    margin: .25rem auto 1.5rem
}

.hero-text .nav-list {
    justify-content: center;
    padding-right: 2.5rem;
}

.hero-text .nav-link {
    color: #fff;
    font-weight: var(--font-bold);
    margin-top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    transition: .3s;
    line-height: 2rem;
    padding: 0 0.5rem;
}

.hero-text .nav-list {
    display: flex;
    margin-bottom: 1.563rem;
    margin-top: 0;
    /* column-gap: 1rem; */
}

.hero-text .nav-link::after {
    content: '';
    height: 2px;
    width: 0%;
    background-color: var(--primary-color);
    display: block;
    transition: .4s ease-in-out;
}

.hero-text .nav-link:hover::after {
    width: 100%;
}

.searchbox-container {
    max-width: 500px;
    margin: auto;
}

@media screen and (max-width: 1280px) {
    .hero-container {
        margin-bottom: 2rem;
    }
}

@media screen and (max-width: 992px) {
    .hero-container {
        height: 400px;
    }

    .image-container {
        height: 400px;
    }

    .hero-text h1 {
        font-size: 3rem;
    }
}

@media screen and (max-width: 768px) {
    .hero-text {
        padding-right: 0.7rem;
        padding-left: 0.7rem;
    }

    .hero-text h1 {
        font-size: var(--h1-font-size);
    }

    .hero-text p, .hero-search {
        font-size: var(--normal-font-size);
    }
}

@media screen and (max-width: 576px) {
    .hero-text h1 {
        font-size: 1.7rem;
    }
}

@media screen and (max-width: 350px) {
    .hero-text h1 {
        font-size: var(--h2-font-size);
    }
}