.mortgage-hero {
    background-color: #f6f6f6;
    margin-top: 3.75rem;
}

.mortgage-hero .hero-container {
    align-items: start;
    margin-bottom: 0;
}

.mortgage-hero h1,
.mortgage-hero p {
    color: rgb(43, 43, 43);
}

.mortgage-hero h1 {
    font-size: 2rem;
    line-height: 2.5rem;
}

.mortgage-hero .heroText-content {
    margin: 0 auto;
    max-width: 560px;
    padding-top: 4.5rem;
}

.mortgage-hero .hero-img {
    object-position: center bottom;
}

.mortgage-hero .nav-list {
    display: none;
}

.large-h2 {
    margin: 0;
    font-size: 2rem;
    line-height: 2.5rem;
    margin-bottom: 1rem;
}

h3 {
    margin: 0;
}

.mortgage-container {
    width: 80%;
    margin: 0px auto;
}

.tools {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.best-mortgage {
    background: #f7f7f7;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    padding: 1.25rem 0.5rem;
}

.bestMortgage-text {
    color: rgb(43, 43, 43);
    font-size: .75rem;
    line-height: 1rem;
}

.bestMortgage-link {
    color: #37c;
}

.mortgage-PopularLinks .links-container {
    padding: 1rem 0.5rem;
}

.mortgageTools-container {
    display: none;
}

@media screen and (max-width: 992px) {
    .mortgage-container {
        width: 100%;
    }
}

@media screen and (max-width: 768px) {
    .large-h2 {
        font-size: 1.5rem;
        line-height: 2rem;
    }

    .mortgage-hero .heroText-content {
        padding-top: 3.5rem;
    }

    .mortgage-hero h1 {
        font-size: var(--h2-font-size);
        line-height: 2rem;
    }

    .mortgageTools-container {
        display: block;
        text-align: center;
        justify-content: center;
    }
    
    .mortgageTools-button {
        background: transparent;
        font-size: var(--normal-font-size);
        text-decoration: underline;
    }

    .mortgageTools-icon {
        display: inline-block;
        height: 1.2rem;
        width: 1.2rem;
        margin-left: 0.2rem;
        margin-bottom: -0.2rem;
    }
}