.header {
    width: 100%;
    top: 0;
    left: 0;
    background-color: #fff;
    border-bottom: 1px solid rgb(202, 202, 202)
}

.nav {
    max-width: 1280px;
    width: 100%;
    height: 3.57rem;
    margin-left: auto;
    margin-right: auto;
    padding: 0 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menu-icon-sm {
    display: none;
}

.title-color {
    color: var(--primary-color)
}

.buttons {
    display: flex;
    column-gap: 0.6rem;
}

.buttons button {
    border: 1px solid rgb(160, 160, 160);
    background-color: #fff;
    padding: 5px 15px;
    border-radius: 0.3rem;
}

.buttons button:hover {
    background-color: rgb(241, 241, 241);
    transition: .3s;
}

.rental-button {
    display: block;
}

.user-icon-sm {
    display: none;
}

.login-button {
    margin-right: var(--mb-1);
    font-size: var(--small-font-size);
}

.signup-button {
    background: var(--primary-color);
    color: #fff;
    padding: 8px 15px;
    border: 1px solid var(--primary-color);
    border-radius: 3.5rem;
    font-size: 0.9rem;
}

.signup-button:hover {
    color: var(--primary-color);
    background: #fff;
    transition: .3s;
}

@media screen and (max-width: 1280px) {
    .nav {
        max-width: 992px;
    }

    .rental-button {
        display: none;
    }
}

@media screen and (max-width: 992px) {
    .user-icon-sm {
        display: block;
    }

    .menu-icon-sm {
        display: block;
    }

    .user-icon {
        width: 1.6rem;
        height: 1.6rem;
        background-color: var(--primary-color);
        color: #fff;
        border-radius: 50%;
        padding: 3px;
    }

    .menu-icon {
        width: 1.5rem;
        height: 1.5rem;
    }

    .items-lg {
        display: none;
    }
}

@media screen and (max-width: 350px) {
    .nav {
        max-width: 90%;
    }
}