.realtorSearch-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 700px;
}

.realtorImg-container {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
}

.realtorImg-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(0.3);
}

.realtorText-container {
    max-width: 1376px;
    width: 100%;
    position: absolute;
    z-index: 1;
    color: #fff;
}

.realtorText-container h1 {
    font-size: 3.5rem;
    line-height: 4rem;
    margin-top: 0;
    margin-bottom: 0.67em;
}

.realtorText-container h3 {
    line-height: 1.75rem;
    margin-top: 0.2rem;
    font-weight: var(--font-normal);
    margin-top: 0.5rem;
    margin-bottom: 1rem;
}

.realtorText-container p {
    margin-top: 1.5rem;
    margin-bottom: .5rem;
    line-height: 1.5rem;
}

.realtorInput-container {
    display: flex;
}

.realtorSearch-container select,
.realtorSearch-container input,
.realtorSearch-container button {
    border-radius: 5px;
    font-size: 18px;
    padding: 1rem;
    margin-right: .7rem;
}

.realtorSearch-container button {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    background-color: var(--primary-color);
    color: #fff;
}

@media screen and (max-width: 1500px) {
    .realtorSearch-container {
        height: 500px;
    }

    .realtorText-container {
        max-width: 1100px;
    }
}

@media screen and (max-width: 1200px) {
    .realtorText-container {
        max-width: 900px;
    }
}

@media screen and (max-width: 992px) {
    .realtorText-container {
        max-width: 750px;
    }

    .realtorText-container h1 {
        font-size: 3rem;
        line-height: 3.5rem;
    }
}

@media screen and (max-width: 768px) {
    .realtorText-container {
        max-width: 570px;
        padding-right: 1rem;
        padding-left: 1rem;
    }

    .realtorText-container h1 {
        font-size: var(--h1-font-size);
        line-height: 3rem;
        margin-bottom: 0;
    }

    .realtorText-container h3 {
        font-size: var(--normal-font-size);
        line-height: 1.25rem;
    }

    .realtorInput-container {
        display: flex;
        flex-wrap: wrap;
    }

    .realtorSearch-container .first-input {
        width: 60%;
    }

    .realtorSearch-container .last-input,
    .realtorSearch-container button {
        margin-top: 0.5rem;
    }

    .realtorSearch-container .last-input {
        width: 65%;
    }
}

@media screen and (max-width: 576px) {
    .realtorText-container h1 {
        font-size: var(--mid-font-size);
        line-height: 2.5rem;
    }

    .realtorText-container h3,
    .realtorSearch-container select,
    .realtorSearch-container input,
    .realtorSearch-container button,
    .realtorSearch-container p {
        font-size: var(--small-font-size);
    }

    .realtorSearch-container select,
    .realtorSearch-container input,
    .realtorSearch-container button {
        margin-right: .6rem;
        padding: 0.6rem;
    }

    .realtorSearch-container button {
        padding-right: 1.3rem;
        padding-left: 1.3rem;
    }
}