.gadget-container {
    max-width: 1300px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-right: 1rem;
    padding-left: 1rem;
    margin-top: 1.5rem;
}

.gadget-container .gadget-content {
    padding: 1rem 0 0.5rem 0;
}

.gadget-title {
    font-size: var(--mid-font-size);
    line-height: 2.5rem;
    color: #333333;
    margin: 0;
    margin-bottom: 2.5rem;
}

.gadget-container .gadget-content .loanText-content {
    padding: 0 2rem;
}

.gadget-container .gadget-content .loanText-container p {
    padding-top: 2rem;
}

.gadget-container .gadget-content .realtor-benefits {
    padding: 1rem 0 0.5rem;
}

.gadget-container .gadget-content .realtor-benefits .gadget-title {
    margin-top: 2rem;
    margin-bottom: 2.5rem;
    padding-left: 1rem;
}

.gadget-container .gadget-content .realtor-benefits .realtor-benefit {
    display: flex;
    flex-wrap: wrap;
}

.gadget-container .gadget-content .realtor-benefits .realtor-benefit .benefit-list {
    display: flex;
    padding-right: 0.75rem;
    flex: 0 0 33.333%;
    max-width: 33.333%;
}

.gadget-container .gadget-content .realtor-benefits .realtor-benefit .benefit-list .benefit-container{
    display: flex;
    flex-wrap: wrap;
}

.benefit-container .benefit {
    flex: 0 0 16.666%;
    max-width: 16.666%;
    padding-right: 1rem;
    padding-left: 1rem;
}

.benefit-container p {
    padding: 0 0.5rem;
    font-size: var(--h3-font-size);
    line-height: 1.75rem;
    flex: 0 0 83.333%;
    max-width: 83.333%;
    height: auto;
}

.realtorBtn-container {
    text-align: center;
    margin-top: 2.5rem;
}

.realtorNews-container {
    display: flex;
    margin: 3rem 1rem;
}

.realtorNews-container .card {
    flex: 0 0 25%;
    max-width: 25%;
    margin-right: 1rem;
}

.for-realtors {
    margin-bottom: 1.5rem;
}

.forRealtors-container {
    padding: 0 1rem;
    width: 50%;
}

.forRealtors-container p {
    line-height: 1.5rem;
}

.forRealtors-container ul {
    padding: 0;
}

.forRealtors-container ul li {
    padding: 5px 0;
}

.forRealtors-container ul li .realtor-link {
    color: rgb(51, 119, 204);
    padding: 1px;
    height: 1.25rem;
}

.realtors-popularLinks .links-container {
    padding: 1rem 0;
    border-top: none;
}

.realtors-text {
    padding: 1rem;
}

.realtors-text p {
    margin-bottom: 1rem;
    font-size: var(--small-font-size);
    line-height: 1.43;
    color: rgb(35, 35, 35);
}

.realtors-text button {
    display: inline-flex;
    background: transparent;
    text-transform: uppercase;
    line-height: 1;
    margin: 0;
    padding: 0;
    color: rgb(30, 97, 217);
    align-items: center;
    font-size: .75rem;
    font-weight: var(--font-bold);
}

.realtors-text .seeMore-button {
    display: inline-block;
    width: 1.2rem;
    height: 1.2rem;
    font-size: 2rem;
    margin-bottom: 0.2rem;
    margin-left: 0.3rem;
}

@media screen and (max-width: 1200px) {
    .gadget-container {
        max-width: 992px;
        padding: 0;
    }

    .gadget-container .gadget-content .loanText-container p {
        font-size: var(--normal-font-size);
        padding-top: 1rem;
    }

    .realtorNews-container .card {
        flex: 0 0 30%;
        max-width: 30%;
    }

    .forRealtors-container {
        width: 100%;
    }

    .forRealtors-container ul li {
        padding: 10px 0 20px;
    }
}

@media screen and (max-width: 992px) {
    .gadget-container {
        max-width: 768px;
    }

    .gadget-container .gadget-content .realtor-benefits .gadget-title {
        margin-bottom: 2rem;
    }
    
    .gadget-container .gadget-content .realtor-benefits .realtor-benefit {
        flex-direction: column;
    }

    .gadget-container .gadget-content .realtor-benefits .realtor-benefit .benefit-list {
        display: block;
        max-width: 100%;
        padding-right: 0;
    }

    .benefit-container .benefit {
        max-width: 10%;
        padding-right: 0;
        padding-left: 5px;
    }

    .benefit-container p {
        font-size: var(--normal-font-size);
        padding-right: 0;
        height: 5.5rem;
    }

    .realtorBtn-container {
        margin-top: 1rem;
    }

    .realtorNews-container .card {
        flex: 0 0 40%;
        max-width: 40%;
    }
}

@media screen and (max-width: 768px) {
    .gadget-container {
        max-width: 560px;
    }

    .gadget-title {
        margin-bottom: 1rem;
    }

    .gadget-container .gadget-content h2 {
        font-size: var(--h2-font-size);
    }

    .gadget-container .gadget-content .heroLoan-container {
        display: block;
    }

    .gadget-container .gadget-content .heroLoan-container .loanText-container {
        flex: 0 0 100%;
        max-width: 100%;
        height: auto;
        order: 1;
    }

    .gadget-container .gadget-content .heroLoan-container .loanText-content {
        padding: 1rem 0;
    }

    .gadget-container .gadget-content .heroLoan-container .img-container {
        flex: 0 0 100%;
        max-width: 100%;
        height: 300px;
    }

    .realtorNews-container .card {
        flex: 0 0 48%;
        max-width: 48%;
    }

    .forRealtors-container p {
        line-height: 1.5rem;
    }

    .realtors-popularLinks .links-container {
        padding: 0;
        padding-bottom: 1rem;
    }
}

@media screen and (max-width: 576px) {
    .gadget-container {
        width: 100%;
    }

    .gadget-title {
        padding: 0 1rem;
    }

    .for-realtors {
        padding: 0;
    }

    .gadget-container .gadget-content .heroLoan-container .loanText-content {
        padding-left: 1rem;
        padding-right: 1rem;
    }

    .gadget-container .gadget-content .realtor-benefits .realtor-benefit {
        padding-right: 1rem;
        padding-left: 1rem;
    }

    .benefit-container p {
        height: 8rem;
        padding-left: 1.5rem;
    }
}

@media screen and (max-width: 425px) {
    .benefit-container .benefit {
        padding-top: 1.6rem;
    }

    .realtorNews-container {
        flex-direction: column;
    }

    .realtorNews-container .card {
        flex: 0 0 100%;
        max-width: 100%;
        margin-right: 0;
        width: 100%;
        margin-top: 1rem;
    }
}