.links-container {
    padding: 5rem 0;
    border-top: 1px solid rgb(160, 160, 160)
}

.link-categories {
    display: grid;
    grid-template-columns: repeat(4, 25%);
}

.link-categories h2 {
    font-size: var(--small-font-size);
    line-height: 1.25rem;
    font-weight: var(--font-normal);
    color: rgb(43, 43, 43);
    margin-bottom: 0.625rem;
}

.link-categories ul {
    padding: 0;
    margin: 0;
}

.link-categories a {
    display: block;
    font-size: .75rem;
    text-decoration: underline;
    color:  rgb(30, 97, 217);
    margin-bottom: 5px;
    padding: 0;
    line-height: 1;
}

.link-categories button {
    display: inline-flex;
    background: transparent;
    text-transform: uppercase;
    line-height: 1;
    margin: 0;
    padding: 0;
    color: rgb(30, 97, 217);
    align-items: center;
    font-size: .75rem;
    font-weight: var(--font-bold);
}

.link-categories .seeMore-button {
    display: inline-block;
    width: 1.2rem;
    height: 1.2rem;
    font-size: 2rem;
    margin-bottom: 0.2rem;
    margin-left: 0.3rem;
}

@media screen and (max-width: 992px) {
    .links-container {
        padding: 2rem 0;
    }
    
    .link-categories {
        grid-template-columns: repeat(2, 50%);
    }
}

@media screen and (max-width: 768px) {
    .link-categories {
        grid-template-columns: auto;
    }
}