.seller-card {
    position: relative;
    flex: 0 0 33.333%;
    max-width: 33.333%;
    width: 100%;
    padding-right: 1rem;
    padding-left: 1rem;
}

.seller-card .card-content {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    height: calc(100% - 24px);
    margin-bottom: 1.5rem;
    align-content: flex-start;
    border-radius: .25rem;
    border: 1px solid rgb(223, 223, 223);
}

.seller-card .img-wrapper {
    width: 240px;
    height: 120px;
    margin: 2rem 1.5rem 0rem;
}

.seller-card .img-wrapper img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    object-fit: contain;
    max-width: 100%;
    max-height: 100%;
}

.seller-card .text-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: calc(100% - 168px);
    padding-top: 0;
    margin-bottom: 1.5rem;
    width: 100%;
    padding: 0.5rem 1rem;
}

.seller-card .text-wrapper h2 {
    margin: 1rem 0;
    text-align: center;
    line-height: 2rem;
}

.seller-card .text-wrapper p {
    line-height: 1.5rem;
    margin-bottom: 1.5rem;
}

.button-wrapper {
    display: flex;
    flex-direction: column;
}

.text-wrapper .search-container input[type=search] {
    font-size: var(--small-font-size);
}

.text-wrapper .search-container button[type=submit] {
    height: 1.7rem;
    width: 1.7rem;
}

.text-wrapper .search-container {
    padding: 0.2rem 0.6rem;
}

.text-wrapper .search-icon {
    padding-top: .1rem;
    height: 1rem;
    width: 1rem;
}

@media not screen and (min-width: 768px) and (max-width: 1280px) {
    .seller-card .img-wrapper {
        flex-basis: 100%;
    }
}

@media screen and (min-width: 768px) and (max-width: 992px) {
    .seller-card .text-wrapper {
        margin-top: 1rem;
    }

    .seller-card .card-content {
        flex-wrap: nowrap;
    }
}

@media screen and (max-width: 992px) {
    .seller-card {
        flex: 0 0 100%;
        max-width: 100%;
    }
}