p {
    margin: 0;
}

.discoverCard-container {
    display: flex;
    align-items: stretch;
    padding: 0;
    position: relative;
    width: 100%;
    flex: 0 0 33.333%;
    min-width: 33.333%;
    box-sizing: border-box;
}

.discover-card {
    position: relative;
    display: flex;
    align-items: stretch;
    min-height: 250px;
    width: 100%;
    padding: 1rem;
    margin: 0.5rem;
    text-align: center;
    border: 1px solid rgb(202, 202, 202);
    border-radius: 0.375rem;
}

.discover-card a {
    display: flex;
}

.discover-card .discoverCard-content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.discover-card .discoverCard-heading {
    display: flex;
    align-items: stretch;
}

.discover-card .discoverCard-heading .discoverCard-img-lg {
    margin-left: auto;
    display: flex;
    justify-content: center;
}

.discover-card .discoverCard-text {
    flex: 1 1 0%;
    margin-top: 0.5rem;
    font-size: var(--normal-font-size);
    line-height: 1.25rem;
    color: rgb(43, 43, 43);
    text-align: left;
}

.discover-card .discoverCard-heading .discoverCard-title {
    font-size: var(--h3-font-size);
    line-height: 1.75rem;
    text-align: left;
    flex-grow: 1;
    max-width: 216px;
    margin: 0;
}

.discoverCard-content span {
    margin-top: 1rem;
    font-size: var(--normal-font-size);
    font-weight: var(--font-bold);
    line-height: 1.5rem;
    color: rgb(43, 43, 43);
    text-align: left;
    position: absolute;
    bottom: 1rem;
    text-decoration: underline;
}

.discover-card .discoverCard-content .discoverCard-text-sm, 
.discover-card .discoverCard-img-sm {
    display:  none;
}

@media screen and (max-width: 992px) {
    .discoverCard-container {
        flex: 0 0 100%;
        min-width: 100%;
    }

    .discover-card {
        width: 100%;
        min-height: 140px;
        flex-shrink: 1;
    }
    
    .discover-card .discoverCard-heading .discoverCard-img-lg,
    .discover-card .discoverCard-content .discoverCard-text {
        display: none;
    }

    .discover-card .discoverCard-img-sm {
        margin-right: 2rem;
        display: inline-block;
        flex-shrink: 0;
        height: 56px;
    }

    .discover-card .discoverCard-img-sm img {
        width: 56px;

    }

    .discover-card .discoverCard-heading .discoverCard-title {
        font-size: var(--normal-font-size);
        line-height: 1.75rem;
        min-width: 100%;
        padding-left: 0;
    }

    .discover-card .discoverCard-content .discoverCard-text-sm {
        display: block;
        margin-top: 0.75rem;
        font-size: 1rem;
        line-height: 1.5rem;
        text-align: left;
    }
}

@media screen and (max-width: 768px) {
    .discover-card {
        padding: 1rem 0.5rem;
    }

    .discover-card .discoverCard-heading .discoverCard-title {
        line-height: 1.25rem;
    }

    .discoverCard-content span {
        margin-top: 0.5rem;
    }
}

@media screen and (max-width: 576px) {
    .discover-card {
        /* width: 90%; */
        min-height: 130px;
        padding: 0.5rem;
    }
}

@media screen and (max-width: 479px) {
    .discover-card {
        min-height: 160px;
    }
    .discover-card .discoverCard-img-sm {
        margin-right: 1rem;
    }
}

