.dropdown-container {
    border-bottom: 1px solid rgb(202, 202, 202);
    padding: 1rem 0;
    z-index: 1040;
    background-color: #fff;
    display: block;
    left: 0;
    position: absolute;
    top: 3.58rem;
    width: 100%;
}

.dropdown {
    max-width: 1280px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-left: 1rem;
    padding-right: 1rem;
}

.dropdown section {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    margin-top: 1rem;
}

.dropdown section h4 {
    margin: 0;
    font-size: var(--small-font-size);
}

.dropdown section ul {
    margin: 1rem 0;
    padding: 0;
    list-style: none;
}

.dropdown section ul li {
    line-height: 1.25rem;
    padding: 0;
    font-size: var(--small-font-size);
    line-height: 1.75rem;
}

.dropdown section ul li a:hover {
    text-decoration: underline;
}

@media screen and (max-width: 1280px) {
    .dropdown {
        max-width: 992px;
    }
}

@media screen and (max-width: 992px) {
    .dropdown {
        max-width: 768px;
        width: 100%;
    }
}