.nav-list {
    display: flex;
    /* column-gap: 1rem; */
}

.nav-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    font-weight: var(--font-normal);
    transition: .3s;
    line-height: 3.3rem;
    padding: 0 0.5rem;
    margin-top: .2rem;
}

.nav-link::after {
    content: '';
    height: 2px;
    width: 0%;
    background-color: var(--primary-color);
    display: block;
    transition: .4s ease-in-out;
}

.nav-link:hover::after {
    width: 100%;
}