.myHome-container {
    margin-bottom: 2rem;
}

.myHome-container .hero-text {
    width: 60%;
}

.myHome-container .hero-container {
    margin-bottom: 2rem;
}

.myHome-container h2 {
    font-size: var(--mid-font-size);
    line-height: 2rem;
    margin: 0;
}

.myHome-container .my-home {
    display: flex;
    justify-content: center;
    place-items: center;
    margin: 1rem 0;
    border: 1px dashed rgb(202, 202, 202);
    border-radius: .5rem;
    min-height: 250px;
}

.myHome-container .my-home img {
    width: 75px;
    height: 75px;
}

.myHome-container .my-home p {
    line-height: 1.5rem;
    font-weight: var(--font-semi-bold);
    color: rgb(117, 117, 117);
    padding-left: 2rem;
}

.myHome-container .myHome-cards {
    padding-top: 4rem;
    padding-bottom: 3rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-right: -0.5rem;
    margin-left: -0.5rem;
}

.myHome-container .myHome-card {
    flex: 0 0 25%;
    max-width: 25%;
    width: 100%;
    padding-right: 0.5rem;
    padding-left: 0.5rem;
}

.myHome-container .myHome-card .myHome-img {
    display: flex;
    justify-content: center;
    align-items: center;
}

.myHome-container .myHome-card .myHome-img img {
    height: auto;
    width: 100%;
}

.myHome-container .myHome-card .myHome-text {
    position: initial;
    padding: 1.25rem 0 0;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.myHome-container .myHome-card .myHome-text h3 {
    margin-bottom: 0.5rem;
    line-height: 2rem;
    font-size: var(--h2-font-size);
    text-align: center;
}

.myHome-container .myHome-card .myHome-text p {
    line-height: 1.5rem;
    text-align: center;
    color: rgb(43, 43, 43);
}

.myHome-container .market-listing {
    margin-top: 3rem;
}

.myHome-container .market-listing .marketList-heading {
    padding-top: 3rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    border-top: 1px solid rgb(223, 223, 223);
    padding-bottom: 1rem;
}

.myHome-container .market-listing .marketList-heading h3 {
    line-height: 1.75rem;
}

.myHome-container .market-listing .marketListIcon-wrapper {
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-right: 0.5rem;
}

.myHome-container .marketList-content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.myHome-container .marketList-content ul {
    padding: 0 1rem;
    flex: 0 0 50%;
    max-width: 50%;
    width: 100%;
}

.myHome-container .marketList-content .market-list {
    font-size: .75rem;
    line-height: 1;
    text-decoration: underline;
    color: rgb(30, 97, 217);
}

@media screen and (max-width: 992px) {
    .myHome-container .hero-text {
        width: 80%;
    }

    .myHome-container .myHome-card {
        flex: 0 0 50%;
        max-width: 50%;
        margin-bottom: 2rem;
    }

    .myHome-container .market-listing {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 678px) {
    .myHome-container .hero-text {
        width: 90%;
    }

    .myHome-container .myHome-cards {
        padding-bottom: 0;
    }

    .myHome-container .myHome-card,
    .myHome-container .marketList-content ul {
        flex: 0 0 100%;
        max-width: 100%;
    }

    .myHome-container .myHome-card {
        margin-bottom: 3rem;
    }

    .myHome-container .marketList-content ul {
        margin: 0;
    }

    .myHome-container .marketList-content {
        display: flex;
    }

    .myHome-container .myHome-card .myHome-text {
        max-width: 393px;
        margin-left: auto;
        margin-right: auto;
    }

    .myHome-container .market-listing {
        margin-top: 0;
    }
}