.box-border {
    margin: 0px 0px 1rem;
    padding: 1rem;
    position: relative;
    border-radius: 0.25rem;
    border: 1px solid rgb(223, 223, 223);
}

.box-border p {
    margin-bottom: 0;
    line-height: 1.5rem;
    font-size: 1rem;
}

.box-border p span {
    font-weight: var(--font-bold);
}

.link-style {
    text-decoration: underline;
    color: rgb(30, 97, 217);
    transition: .5s;
}

.link-style:hover {
    color: rgb(20, 65, 147);
}

.equityinfo-content {
    display: flex;
    flex-direction: row;
}

.equity-img {
    display: flex;
    justify-content: center;
    padding-right: 1rem;
    margin-right: auto;
    margin-left: auto;
}

.equity-img img {
    height: 150px;
    width: 230px;
}

.equity-text {
    padding: 0.25rem;
    padding-left: 1.25rem;
    position: relative;
}

.equity-text h2 {
    margin: 0;
    line-height: 2rem;
    margin-bottom: .75rem;
}

.equity-text p {
    font-size: var(--small-font-size);
    line-height: 1.25rem;
    margin-bottom: 0.75rem;
}

.equity-text .equityText-button {
    margin-top: 0.25rem;
}

.equity-text .equityText-link {
    position: relative;
    display: inline-flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    left: 2.5rem;
    font-size: var(--small-font-size);
    margin-top: 0.25rem;
    margin-bottom: 1.5rem;
    text-decoration: underline;
    color: rgb(30, 97, 217);
}

@media screen and (max-width: 768px) {
    .box-border {
        margin: .5rem 0px 1.25rem;
    }

    .equityinfo-content {
        flex-wrap: wrap;
    }

    .equity-text {
        padding: 0;
    }

    .equity-text h2{
        font-size: 1.25rem;
        line-height: 1.75rem;
    }

    .equity-text .equityText-button {
        display: flex;
        width: 100%;
    }

    .equity-text .equityText-link {
        display: flex;
        width: 100%;
        margin-top: 1rem;
        left: 0px;
    }
}