.img-container {
    /* position: relative;
    height: 100%; */
    position: relative;
    padding-right: 0;
    padding-left: 0;
    height: 500px;
    flex: 0 0 50%;
    max-width: 50%;
}

.img-container img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
}