@media screen and (max-width: 992px) {
    .sideMenu-backdrop, .sidemenu {
        position: fixed;
        top: 0px;
        left: 0px;
        height: 100%;
    }

    .sideMenu-backdrop {
        pointer-events: auto;
        background-color: rgba(0, 0, 0, 0.8);
        width: 100%;
        display: block;
        z-index: 1038;
    }

    .sidemenu {
        width: 75%;
        margin: 0;
        max-width: 280px;
        overflow-y: auto;
        background-color: #fff;
        z-index: 1039;
        transition: all 0.5s ease-out;
    }

    .top-menu {
        align-items: flex-end;
        display: flex;
        justify-content: space-between;
    }

    .topMenu-logo {
        margin: 1rem;
        width: 148px;
    }

    .topMenu-logo h2 {
        margin: 0;
    }

    .top-menu button {
        background-color: #fff;
        border: none;
        margin-right: .5rem;
        margin-bottom: .8rem;
    }

    .top-menu .close-button {
        height: 25px;
        width: 25px;
    }

    .menu-content {
        margin-left: auto;
        line-height: 50px;
    }

    .menu-list {
        padding: 0;
        width: 100%;
        margin-right: auto;
        margin-left: auto;
    }

    .menu-list ul {
        padding: 0;
        margin: 0;
    }
    .menu-list ul li {
        position: relative;
    }

    .menu-list ul li:first-child,
    .submenu-container section ul li:first-child {
        border-top: 1px solid rgb(202, 202, 202);
    }

    .menu-list .main-menu {
        border-bottom: 1px solid rgb(202, 202, 202);
    }

    .menu-list .main-menu a {
        display: block;
        padding: 0 1rem;
        color: rgb(43, 43, 43);
        font-size: var(--small-font-size);
    }

    .menu-list .main-menu button {
        position: absolute;
        right: 0px;
        top: 0px;
        padding: 0 1rem;
        height: 50px;
        display: inline-block;
        text-align: center;
        vertical-align: top;
        background-color: transparent;
        color: rgb(43, 43, 43);
        border: none;
    }

    .menu-list .main-menu .submenu-button {
        display: inline-block;
        width: 1rem;
        height: 1rem;
        font-size: var(--normal-font-size);
    }

    .menu-list .sub-menu {
        display: block;
        border-bottom: 1px solid rgb(202, 202, 202);
        padding: 1rem 0;
        transition: .4s ease-in;
    }

    .menu-list .sub-menu .submenu-container {
        width: 100%;
        margin-right: auto;
        margin-left: auto;
        padding-left: 1rem;
        padding-right: 1rem;
    }   
    
    .submenu-container section ul {
        margin: 1rem 0;
    }

    .submenu-container section ul li {
        line-height: 1.75rem;
    }

    .submenu-container section h4 {
        line-height: 1.25rem;
        font-size: var(--small-font-size);
        margin: 0;
    }

    .submenu-container section ul li a {
        font-size: var(--small-font-size);
    }
}