.footer-container {
    min-height: 600px;
    flex-shrink: 0;
    background-color: rgb(43, 43, 43);
}

.footer {
    display: block;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}

.footer-top {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 1rem;
}

.footer-top ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
}

.footer-top ul li a {
    background-color: #fff;
    color: #000;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 0 40%;
    padding: 0 .5rem;
    height: 2.5rem;
    margin-bottom: 1rem;
    font-size: 1.5rem;
}

.footer-top ul li a:hover{
    background-color: rgb(207, 207, 207);
    border-radius: 40% 0;
    transition: .3s;
}

.footer-bottom ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0px 0px 2rem;
    padding: 0;
}

.footer-bottom ul li {
    width: auto;
    padding: 0.5rem 1rem 0.5rem 0;
}

.footer-bottom ul li a {
    text-align: left;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    font-size: var(--small-font-size);
    height: 2.25rem;
    color: #fff;
    padding: 0;
}

.footer-bottom ul li a:hover {
    color: rgb(194, 194, 194);
    text-decoration: underline;
    transition: .3s;
}

.footer-bottom ul li .footerLink-other {
    color: rgb(244, 182, 48);
    font-weight: var(--font-bold);
}

.footer-bottom h2 {
    text-transform: uppercase;
    margin-bottom: 1rem;
    font-size: var(--small-font-size);
    line-height: 1.25rem;
    color: #fff;
    font-weight: 700;
}

.footer-bottom p {
    color: #fff;
    text-align: center;
    font-size: var(--smaller-font-size);
}

.footerList-icons {
    justify-content: center;
    width: auto;
}

.footer-top ul li {
    padding-right: 1rem;
}

.footer-top ul li:last-child {
    padding-right: 0;
}

@media screen and (max-width: 1280px) {
    .footer-bottom ul li {
        width: 25%;
    }
}

@media screen and (max-width: 992px) {
    .footer-container {
        min-height: 496px;
    }
}

@media screen and (max-width: 768px) {
    .footer-container {
        min-height: 532px;
    }

    .footer-top {
        justify-content: center;
    }

    .footer-bottom ul li {
        width: 33.33%;
    }
}

@media screen and (max-width: 624px) {
    .footer-container {
        min-height: 588px;
    }
}

@media screen and (max-width: 576px) {
    .footer-top, .footer-bottom {
        padding: 0 1rem;
    }
}

@media screen and (max-width: 479px) {
    .footer-bottom ul li {
        width: 50%;
    }
}

@media screen and (max-width: 370px) {
    .footer-top ul li {
        padding-right: .3rem;
    }
}