.rate-container {
    margin-top: 3.75rem;
    background: #f6f6f6;
    padding: 3rem 0;
}

.rate-container h2 {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5rem;
}

.rate-container .rate-subtitle {
    font-size: var(--normal-font-size);
    line-height: 1.5rem;
}

.current-rate {
    border-bottom: 1px solid rgb(220, 224, 228);
    margin-top: 1.5rem;
}

.rate-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 1.5rem;
}

.rate-card {
    position: relative;
    flex-basis: 30%;
    border: 1px solid rgb(202, 202, 202);
    box-shadow: rgba(0, 0, 0, 0.11) 0px 1px 5px;
    background-color: #fff;
    border-radius: .25rem;
    transition: .3s;
}

.rate-card:hover {
    border-color: #000;
    box-shadow: none;
}

.rate-link {
    display: block;
    padding: 1.5rem 1rem;
}

.rateCard-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    padding: 0;
}

.rateCard-content .rate-title {
    margin-bottom: 0;
    font-weight: var(--font-bold);
    font-size: var(--h3-font-size);
    line-height: 1.75rem;
    display: flex;
    align-items: center;
}

.rate-icon {
    display: inline-block;
    width: 1rem;
    height: 1rem;
    font-size: var(--normal-font-size);
}

.rate-percentage {
    font-weight: var(--font-bold);
    font-size: 2rem;
    line-height: 2.5rem;
}

.mortgage-advertising {
    margin-top: 1.5rem;
}

.mortgage-advertising .advertising-button {
    background-color: #f6f6f6;
}

.rate-info {
    margin-top: 0.5rem;
    margin-bottom: 2.25rem;
    font-size: var(--smaller-font-size);
}

.rate-info .advertising-button {
    font-size: var(--smaller-font-size);
}

.refinance-equity {
    padding-top: 3rem;
}

.refinance-equity .refinance-equityText {
    font-size: 1rem;
    line-height: 1.5rem;
    font-weight: var(--font-bold);
    margin-bottom: 1rem;
}

.refinance-equity .tools {
    justify-content: start;
}

.refinance-equity .tool-card:last-child {
    margin-left: 1.5rem;
}

@media screen and (max-width: 1280px) {
    .rateCard-content {
        flex-direction: column;
    }

    .rateCard-content span {
        margin-bottom: 1rem;
    }

    .refinance-equity .tools {
        justify-content: space-between;
    }

    .refinance-equity .tool-card:last-child {
        margin-left: 0;
    }
}

@media screen and (max-width: 768px) {
    .rate-container {
        margin-top: 2.5rem;
    }

    .rate-list {
        flex-direction: column;
    }

    .rate-container .rate-subtitle {
        line-height: 1.25rem;
    }

    .rate-card {
        margin-bottom: 1rem;
    }

    .rateCard-content {
        flex-direction: row;
    }

    .rate-link {
        padding: 1rem;
    }
}