.state-details-container {
    position: relative;
    margin-top: 4rem;
    margin-bottom: 3.75em;
    padding: 0 1rem;
}

.state-details-container h2 {
    margin-bottom: 1rem;
}

.details-wrapper {
    text-align: center;
    margin-top: 1.875rem;
    margin-bottom: .625rem;
}

.details-list {
    padding-left: 0;
    display: flex;
    justify-content: space-between;
}

.details-list li a {
    font-size: 2rem;
    line-height: 2.5rem;
    color: #37c;
}

.details-list li a:hover {
    text-decoration: underline;
}

.details-list li p {
    font-size: 1rem;
    line-height: 1.5rem;
    margin: 0;
    color: rgb(117, 117, 117);
}

@media screen and (max-width: 992px) {
    .details-list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    
    .details-list li {
        padding: 0.625rem;
    }
}

@media screen and (max-width: 768px) {
    .state-details-container h2 {
        font-size: var(--h3-font-size);
        line-height: 1.75rem;
    }

    .state-details-container li a {
        font-size: var(--h2-font-size);
        line-height: var(--mid-font-size);
    }
}