.h2-container {
    position: relative;
    margin-bottom: 2.5rem;
    max-width: 1280px;
    width: 100%;
    padding: 0 1rem;
    margin-left: auto;
    margin-right: auto;
}

.h2-container h2 {
    margin: 0;
    font-size: 2rem;
    line-height: 2.5rem;
}

@media screen and (max-width: 1280px) {
    .h2-container {
        max-width: 992px;
        margin-bottom: 2rem;
    }
}

@media screen and (max-width: 992px) {
    .h2-container {
        max-width: 768px;
        margin-bottom: 1.5rem;
    }
}

@media screen and (max-width: 768px) {
    .h2-container h2 {
        font-size: 1.5rem;
    }
}