.card {
    position: relative;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 8px;
    border-radius: .25rem;
    overflow: hidden;
}

.img-wrapper {
    position: relative;
    overflow: hidden;
}

.card-img {
    display: block;
    height: 200px;
    width: 100%;
    object-fit: cover;
}

.time-badge {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    position: absolute;
    top: .813rem;
    left: 1rem;
    background-color: rgb(26, 82, 184);
    color: #fff;
    text-transform: uppercase;
    border-radius: .25rem;
    height: 1.1rem;
    line-height: 1;
    padding: 0.125rem 0.5rem;
    font-size: .75rem;
    font-weight: var(--font-bold);
}

.img-icon, .filled-icon {
    position: absolute;
    bottom: .5rem;
    right: 1rem;
    height: 3rem;
    width: 3rem;
    cursor: pointer;
}

.img-icon {
    color: #fff;
}

.filled-icon {
    color: transparent;
}

.filled-icon:hover {
    color: var(--primary-color);
    transition: .15s ease-in-out;
}

.card-details {
    padding: 0.5rem 1rem;
}

.card-header {
    display: flex;
    align-items: center;
}

.badge {
    height: 0.75rem;
    width: 0.75rem;
    background-color: rgb(54, 179, 150);
    margin-right: 0.25rem;
    border-radius: 0.25rem;
}

.badge-sold {
    background-color: rgb(245, 97, 64);
}

.room-details {
    display: flex;
    margin: 0;
    padding-left: 0;
    font-size: var(--small-font-size);
}

.list-margin {
    margin-right: 0.25rem;
    font-weight: var(--font-semi-bold);
}

.room-details li:not(:first-child) {
    margin-left: 1rem;
}

.card-details p, .room-details li {
    margin: 0;
    font-size: var(--small-font-size);
    color: rgb(43, 43, 43);
    line-height: 1.2rem;
}

.card-details h2 {
    margin-top: 0.3rem;
    font-size: 1.25rem;
    font-weight: 700;
}

@media screen and (max-width: 576px) {
    .card-img {
        height: 150px;
    }

    .card-details {
        padding: 0.3rem;
    }

    .room-details li:not(:first-child) {
        margin-left: .5rem;
    }

    .list-margin {
        margin-right: 0.1rem;
    }
}