.heroLoan-container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-left: 1rem;
    padding-right: 1rem;
}

.first-row {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-right: -1rem;
    margin-left: -1rem;
}

@media screen and (max-width: 768px) {
    .heroLoan-container {
        display: none;
    }
}