.terms-container {
    margin-top: 2rem;
}

.terms-h2 {
    margin: 0;
    line-height: 2rem;
}

.terms-header {
    margin-top: 2rem;
    display: flex;
}

.terms-content p {
    line-height: 1.5rem;
}

.header-bullet {
    width: 0.25rem;
    height: 1.5rem;
    background-color: var(--primary-color);
    margin-right: 0.5rem;
}

.terms-title {
    font-weight: var(--font-bold);
    margin-bottom: 0.75rem;
}

.terms-text {
    color: rgb(43, 43, 43);
}

@media screen and (max-width: 768px) {
    .terms-h2 {
        font-size: var(--h3-font-size);
        line-height: 1.75rem;
    }

    .terms-content p {
        line-height: 1.25rem;
    }
}