.tool-card {
    display: flex;
    flex-direction: row;
    flex-basis: 31.5%;
    margin-bottom: 1.5rem;
    border-radius: 6px;
    background: #fff;
    border: 1px solid rgb(202, 202, 202);
    padding: 0.75rem 1rem;
    box-shadow: rgba(0, 0, 0, 0.11) 0px 1px 5px;
}

.tool-img {
    display: flex;
    flex-shrink: 0;
    margin-right: 1rem;
    align-items: center;
}

.tool-img img {
    height: 40px;
    width: 40px;
}

.tool-text {
    flex-grow: 1;
}

.tool-text h3 {
    margin-bottom: 0.25rem;
    font-size: var(--normal-font-size);
    line-height: 1.5rem;
}

.tool-text p {
    color: rgb(117, 117, 117);
    font-size: var(--small-font-size);
    line-height: 1.3rem;
}

@media screen and (max-width: 1280px) {
    .tool-card {
        flex-basis: 48%;
    }
}

@media screen and (max-width: 768px) {
    .tool-card {
        flex-basis: 100%;
        margin-bottom: 1rem;
    }
}