.sellerGuide-container {
    padding-top: 2rem;
    padding-bottom: 3.75rem;
    background: rgb(247, 247, 247);
}

.sellerGuide-container .title {
    margin: 0;
    margin-bottom: 1.5rem;
    line-height: 2rem;
}

.sellerGuide-container .sellerGuide-card {
    flex: 0 0 25%;
    max-width: 25%;
    position: relative;
    width: 100%;
    padding-right: 1rem;
    padding-left: 1rem;
}

.sellerGuide-container .card-content {
    display: flex;
    flex-direction: row;
    flex-flow: row wrap;
    align-items: flex-start;
    height: 100%;
    position: relative;
    background-color: #fff;
    border-radius: .25rem;
    padding: 1rem 0.75rem;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 8px
}

.sellerGuide-container .img-wrapper {
    display: flex;
    flex: 2 1 100%;
    justify-content: center;
    align-items: center;
    order: 1;
    margin-bottom: 1.5rem;
}

.sellerGuide-container .title-wrapper {
    flex: 1 1 50%;
    order: 3;
}

.sellerGuide-container .title-wrapper h2 {
    display: inline;
    margin: 0;
    font-size: var(--h3-font-size);
    line-height: 1.75rem;
}

.sellerGuide-container .number-wrapper {
    display: flex;
    margin-right: 0.75rem;
    flex: 0 0 auto;
    order: 2;
    justify-content: center;
    align-items: center;
    width: 2rem;
    height: 2rem;
    line-height: 1rem;
    font-weight: var(--font-bold);
    border-radius: 50%;
    background-color: rgb(221, 227, 242);
}

@media screen and (max-width: 992px) {
    .sellerGuide-container .sellerGuide-card {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 1rem;
    }

    .sellerGuide-container .card-content {
        flex-flow: row;
        align-items: center;
    }

    .sellerGuide-container .number-wrapper {
        order: 1;
    }

    .sellerGuide-container .title-wrapper {
        flex: 4 4 auto;
        order: 2;
    }

    .sellerGuide-container .img-wrapper {
        flex: 3 0 auto;
        justify-content: flex-end;
        order: 3;
    }
}

@media screen and (max-width: 768px) {
    .sellerGuide-container {
        padding-bottom: 1.875rem;
    }

    .sellerGuide-container .title-wrapper {
        margin-right: 0.5rem;
    }

    .sellerGuide-container .title-wrapper h2 {
        font-size: 1rem;
        line-height: 1.25;
    }
}